        triggerSearch: function triggerSearch() {
            console.log('triggerSearch');
            document.getElementById('grants-map').scrollIntoView();
            var app = this;

            app.clearMarkers();

            var places = app.searchBox.getPlaces();

            if (places.length == 0) {
                return;
            }

            console.log('places: ', places.length, places);

            var input = document.getElementById('pac-input');

            var reset = document.getElementById('reset-location');
            if (reset.classList.contains('hidden-reset-loc')) {
                reset.classList.remove('hidden-reset-loc');
            }

            var inRange = app.hideOutsideRadius(places);

            var bounds = new google.maps.LatLngBounds();
            places.forEach(function (place) {
                if (!place.geometry) {
                    console.log("Returned place contains no geometry");
                    return;
                }

                var placeLat = place.geometry.location.lat();
                var placeLng = place.geometry.location.lng();

                var originPlace = new google.maps.LatLng(placeLat, placeLng);

                var icon = {
                    url: place.icon,
                    size: new google.maps.Size(71, 71),
                    origin: new google.maps.Point(0, 0),
                    anchor: new google.maps.Point(17, 34),
                    scaledSize: new google.maps.Size(25, 25)
                };

                var here = 'https://parkpeople.ca/listings/custom/uploads/2018/04/pin_here.svg';

                // Create a marker for each place.
                var markerLabel = "YOU";
                app.markers.push(new google.maps.Marker({
                    map: app.map,
                    icon: here,
                    zIndex: 999999,
                    position: place.geometry.location
                }));
            });

            app.map.fitBounds(bounds);

            console.log('inRange', inRange);
            app.$store.dispatch("setActiveEvents", inRange);
        },
        buildMarkers: function buildMarkers() {
            var app = this;

            // Let's combine this method with rebuildMarkers
            // We can set a variable to contain the right set of locations with a conditional
            app.markers = [];
            app.infoWindows = [];

            /*
                Iterate over all of the events
            */
            for (var i = 0; i < app.locations.length; i++) {

                // Check is event is in TO
                // if (app.locations[i].pc.startsWith("M") && !app.torontoCheck) {

                /*
                Set marker position
                */
                var theposition = new google.maps.LatLng(app.locations[i].lat, app.locations[i].lng);

                /*
                    Choose marker style based on type
                */
                if (app.locations[i].type == 'event') {
                    (function () {

                        /*
                        Add the event listener to open the info window for the marker.
                        */
                        var closeInfoWindows = function closeInfoWindows() {
                            var i = app.infoWindows.length;
                            while (i--) {
                                app.infoWindows[i].close();
                            }
                        };

                        var the_icon = app.pin;

                        /*
                            Create the marker for each of the locations and set the
                            latitude and longitude to the latitude and longitude
                            of the location. Also set the map to be the local map.
                        */

                        var iconSize = new google.maps.Size(42, 40);
                        var marker = new google.maps.Marker({
                            position: theposition,
                            map: app.map,
                            title: app.locations[i].title,
                            icon: {
                                url: the_icon,
                                scaledSize: iconSize
                            }
                        });

                        /*
                            Push the new marker on to the array.
                        */
                        app.markers.push(marker);

                        /*
                            Create the info window and add it to the local
                            array.
                        */
                        // console.log(app.locations[i].listing);
                        var windowString = app.infoWindowString(app.locations[i].slug, app.locations[i].id, app.locations[i].title, app.locations[i].listing[1], app.locations[i].listing[2], app.locations[i].listing[0], app.locations[i].nice_start_date, app.locations[i].start_time, app.locations[i].end_time, app.locations[i].address, app.locations[i].timeframe);

                        var infoWindow = new google.maps.InfoWindow({
                            content: windowString
                        });

                        app.infoWindows.push(infoWindow);

                        google.maps.event.addListener(marker, 'spider_click', function (marker, i) {
                            return function () {
                                closeInfoWindows();
                                app.map.setCenter(marker.getPosition());
                                app.map.panBy(0, -150);
                                infoWindow.setContent(windowString);
                                infoWindow.open(app.map, marker);
                            };
                        }(marker, i));

                        app.oms.addMarker(marker);
                    })();
                } else {
                    return;
                }

                // } //end STARTWITH
            }

            this.map.panBy(100, -100);
        },
